.red-border {
  border: 1px solid var(--error-message) !important;
}

.fade-in {
  opacity: 0;
  -webkit-transform: translateY(80px);
          transform: translateY(80px);
  transition: all 0.8s ease;
}

:root {
  --light-blue: #04c3ff;
  --light-blue-shadow: rgb(69 211 255 / 27%);
  --sea-green: #0abead;
  --sea-green-dark: #0cbfae;
  --sea-green-extra-dark: #09bead;
  --sea-green-shadow: rgba(11, 190, 173, 0.4);
  --header-hamburger-background: rgba(11, 190, 173, 0.8);
  --orange: #f57f17;
  --dark-orange: #ff5823;
  --orange-shadow: rgba(245, 127, 23, 0.2);
  --white: #ffffff;
  --black: #333333;
  --disabled-btn: #09bead7a;
  --error-message: #e44f4fd1;
  --toast-error: #e44f4ffa;
  --toast-shadow: #9c9c9ce3;
  --toast-success: #0cbfae;
  --loader-color: #0cbfae;
}

.App{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-top: 0;
  padding: 0;
}

.app-heading{
  /* position: absolute; */
  z-index: 5555;
  margin: 0;
  padding: 0;
}

/* div{ padding:0; margin:0;} */

.profile-container {
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  height: 10%;
  min-height: -webkit-fit-content;
  min-height: -moz-fit-content;
  min-height: fit-content;
  margin-top: 100px;
}

.profile-parent {
  /* color of those text */
  display: flex;
  align-items: center;
  color: rgb(10,90,64);
}

.profile-details-name {
  /* Welcome Text */
  font-size: 18px;
  font-family: "Poppins SemiBold";
}

.profile-details-role {
  /* Welcome Text */
  display: flex;
  flex-direction: column;
  margin: 14px 0 24px 0;
}

.profile-details-role h1 {
  font-family: Poppins Bold;
  font-size: 40px;
}

.primary-text {
  font-family: Poppins Bold;
  color: (--white);
}

.profile-role-tagline {
  /* Formal Description */
  display: block;
  font-size: 18px;
  float: left; 
  /* margin-right: 100px; */
  font-family: Poppins SemiBold;
  width: 600px;
  height: 300px; 
  text-align:justify;
  margin: auto;
}

