.profile-picture {
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 1px 0px 0px var(--white);
  border-radius: 100%;
  height: 300px;
  width: 300px;
  margin: 0 100px 0 0px;
  background-size: cover;
}

/* For ipad pro*/
@media screen and (max-width: 1110px) {
 

  .profile-details-name {
    font-size: 20px;
  }

  .profile-details-role .PRIMARY-TEXT {
    font-size: 12px;
  }

}

@media screen and (max-width: 970px) {

  .profile-parent{
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 80%;
  }
  .profile-picture {
    height: auto;
    width: 100%;
  }

  .profile-role-tagline {
    /* Formal Description */
    display: block;
    float: left;
    width: 100%;
  }

}
